.card-container {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
  }
  
  .card {
    width: 200px;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    text-align: center;
    background: linear-gradient(135deg, #ff7f50, #ff6347);
    color: white;
  }
  
  .card img {
    width: 100px;
    height: 100px;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  